body {
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
	padding:30px;
	padding-top:0;
	margin:0;
	color:#2f2f2f;
}

h1,h2 {
	font-weight:400;
	color:#000;
}

input {
	padding:0.5em 1em;
	margin-bottom:1em;
	margin-top:0.5em;
	border:1px solid #ccc;
	font-size:16px;
}

table {
border-collapse: collapse;
min-width:418px;
	tbody {
		tr {
			th {
				border:1px solid #ccc;
				text-align: left;
				padding:0.5em 1em;
				background:#f1f1f1;
			}
			td {
				border:1px solid #ccc;
				border-collase: collapse;
				padding:0.5em 1em;
			}
		}
	}
}


